table.dp {
  width: 180px;
  margin-top: 3px;
  background-color: #ffffff;
  border: 1px solid #bbbbbb;
  border-spacing: 0px;
  -moz-box-shadow: 2px 2px 2px #cccccc;
  -webkit-box-shadow: 2px 2px 2px #cccccc;
  box-shadow: 2px 2px 2px #cccccc;
}

.dp td, .tpHour td, .tpMinute td {
  padding: 2px;
  font-size: 11px;
}

td.dpHead {
  padding: 4px;
  font-size: 11px;
  font-weight: bold;
}

/* Barre titre */
.dpHead, .tpHead, .tpHour td:Hover .tpHead {
  background-color: rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(top, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
  background-image: -o-linear-gradient(top, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
  background-image: -moz-linear-gradient(top, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
  background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
  background-image: -ms-linear-gradient(top, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(255, 255, 255, 0.3)), color-stop(1, rgba(0, 0, 0, 0.3)));
  font-size: 10px;
  cursor: auto;
}

/* Barre navigation */
.dpButtons, .tpButtons {
  text-align: center;
  background-color: #eaeaea;
  color: #232323;
  font-weight: bold;
  cursor: pointer;
}

.dpDayNames td, .dpExplanation {
  background-color: #eaeaea;
  font-weight: bold;
  text-align: center;
  font-size: 11px;
}

.dpWeek td {
  text-align: center;
}

.dpToday, .dpReg, .dpSelected {
  cursor: pointer;
}

.dpToday {
  font-weight: bold;
  color: #232323;
  background-color: #dddddd;
}

.dpReg:Hover, .dpToday:Hover {
  background-color: #333333;
  color: #ffffff;
}

/* Jour courant */
.dpSelected {
  background-color: #0B63A2;
  color: #ffffff;
  font-weight: bold;
}

.tpHour {
  border-top: 1px solid #dddddd;
  border-right: 1px solid #dddddd;
}

.tpHour td {
  border-left: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
  cursor: pointer;
}

.tpHour td:Hover {
  background-color: #232323;
  color: #ffffff;
}

.tpMinute {
  margin-top: 5px;
}

.tpMinute td:Hover {
  background-color: #333333;
  color: #ffffff;
}

.tpMinute td {
  background-color: #eaeaea;
  text-align: center;
  cursor: pointer;
}

.fulldaystarthour {
  margin-right: 2px;
}

.fulldaystartmin {
  margin-right: 2px;
}

.fulldayendhour {
  margin-right: 2px;
}

.fulldayendmin {
  margin-right: 2px;
}

/* Bouton X fermer */
.dpInvisibleButtons {
  border-style: none;
  background-color: transparent;
  font-size: 9px;
  border-width: 0px;
  color: #0B63A2;
  vertical-align: middle;
  cursor: pointer;
}

td.dpHead .dpInvisibleButtons {
